<template>
  <div id="app">
    <header id="site-header" v-show="showHeader">
      <div class="container">
        <span>Harmony Management System</span>
        <span class="logout" v-if="isLoggedIn()" @click="logout()"> Log out</span>
      </div>
    </header>
    <main>
      <router-view></router-view>
    </main>
  </div>
</template>

<script>
import './assets/css/style.css'
export default {
  beforeCreate() {
    const uid = localStorage.getItem('laa_uid')
    if (uid)
      this.$store.commit('setUid', uid)
  },
  created(){
    // Track initial page creation
    this.$store.commit('pageCreated')
  },
  methods: {

  },
  mounted() {
    // Register global event listener for ESC key
    document.onkeyup = e => {
      if (e.keyCode == 27)
        this.hideModal()
    }
  },
  name: 'app'
}
</script>

<style>
html { min-height: 100vh}
#site-header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #35495E;
  background-color: #0071c9;
  color: #ffffff;
}
</style>


<style scoped>
body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

header span {
  display: inline-block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}

.logout {
  float: right;
  font-size: 15px;
  font-weight: 200;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.35);
    padding: 6px;
    border-radius: 3px;
    margin-top: 10px;
}

.logout:hover {
  color: #FF9800;
}
</style>



