<template>
    <div class="container">
        <transition enter-active-class="fade-in" leave-active-class="fade-out">
            <div class="modal flex" v-if="! dataLoaded">
                <div class="dialog">
                    <span class="spinner s bdblue"></span>
                    <span>Loading...</span>
                </div>
            </div>
            <div class="data-table" v-else>
                <div v-if="members.length > 0">
                    <p>There are {{members.length}} member(s). <router-link to="/members/new" tag="button">Add new</router-link></p>
                    <table class="table table-bordered table-hover">
                        <thead>
                            <tr>
                                <th>Full name</th>
                                <th>Phone no</th>
                                <th>Address</th>
                                <th>Association</th>
                            </tr>
                        </thead>
                        <tbody class="">
                            <tr v-for="member in members" :key="member.fname">
                                <td>{{member.fname}} {{member.lname}}</td>
                                <td>{{member.phone}}</td>
                                <td>{{member.address}}</td>
                                <td>Association</td>
                            </tr>
                        </tbody>
                    </table>
                </div>
                <div v-else>
                    <p>No members yet.</p>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import 'whatwg-fetch'
import Qs from 'qs'
export default {
    created() {
        fetch(`${this.BASE_URL}/staff/all`)
            .then(res => this.handleResponse(res))
            .then(res => {
                this.members = res;
                this.dataLoaded = true;
            }).catch(err => {
                console.log(err)
                if (!err.body)
                    // Alert error.
                    return this.genericErrorAlert()

                err.body.then(body => {
                    alert(body)
                })
            })
    },
    data() {
        return {
            dataLoaded: false,
            members: [],
            offset: 0,
        }
    },
    mounted() {
        // console.log(this.members);
    }
}
</script>

<style scoped>
.c_loader {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
}

.c_loader::before {
    content: '';
    display: block;
    position: fixed;
}

.c_loader::after {
    content: '';
}
</style>





