import Vue from 'vue'
import Router from 'vue-router'
import Hello from '@/components/Hello'
import Home from '@/components/Home'
import Members from '@/components/Members'
import AddMember from '@/components/AddMember'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/members',
      name: 'Members',
      component: Members
    },
    {
      path: '/members/new',
      name: 'AddMember',
      component: AddMember
    }
  ]
})



