import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        showHeader: true,
        showModal: false,
        pageCreated: false,
        uid: null
    },
    getters: {
        isLoggedIn(state){
            return state.uid
        },
        modalOpen(state){
            return state.showModal
        },
        pageCreated(state){
            return state.pageCreated
        },
        showHeader(state){
            return state.showHeader
        }
    },
    mutations: {
        burn(state){
            localStorage.removeItem('laa_uid')
            return window.location.reload()
        },
        hideHeader(state){
            state.showHeader = false
        },
        pageCreated(state){
            state.pageCreated = true
        },
        setUid(state, uid){
            state.uid = uid
        },
        showModal(state){
            state.showModal = true
        },
        hideModal(state){
            state.showModal = false
        }
    }
})


